  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #a5a2a2;
  height: 100vh;
}
body.fijar-body {
  overflow: hidden;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

main {
  background-image: linear-gradient(to top, #352ed3, #382ed0, #3b2ecc, #3d2ec9, #3f2ec6, #412ec7, #422ec9, #442eca, #462ed0, #482ed5, #4b2edb, #4d2ee1);
  background-repeat: no-repeat;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Open Sans", sans-serif;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #FFFFFF;
  font-size: 1.6rem;
  line-height: 1.8;
  font-family: "Open Sans", sans-serif;
}

section {
  overflow: hidden;
  position: relative;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  transition: 0.5s;
  background: linear-gradient(to top, #352ed3, #382ed0, #3b2ecc, #3d2ec9, #3f2ec6, #412ec7, #422ec9, #442eca, #462ed0, #482ed5, #4b2edb, #4d2ee1);
  background-repeat: no-repeat;
}

section.active {
  background: linear-gradient(to bottom, #352ed3, #382ed0, #3b2ecc, #3d2ec9, #3f2ec6, #412ec7, #422ec9, #442eca, #462ed0, #482ed5, #4b2edb, #4d2ee1);
  background-repeat: no-repeat;
}

section .color {
  position: absolute;
  filter: blur(150px);
}

section .color:nth-child(1) {
  top: -350px;
  width: 600px;
  height: 600px;
  background: #00c9b7;
}

section .color:nth-child(2) {
  bottom: -150px;
  left: 100px;
  width: 500px;
  height: 500px;
  background: #00C9B7;
}

section .color:nth-child(3) {
  bottom: 50px;
  right: 100px;
  width: 500px;
  height: 500px;
  background: #412EC2;
}

.box__square {
  position: relative;
}

.box__square .square {
  position: absolute;
  animation: animate 10s linear infinite;
  animation-delay: calc(-1s * var(--i));
}

@keyframes animate {
  0%, 100% {
    transform: translateY(-40px);
  }
  50% {
    transform: translateY(40px);
  }
}
.box__square .square:nth-child(1) {
  top: -50px;
  right: -60px;
  width: 100px;
  height: 100px;
  background: url(../img/dollar.png);
  background-position: center;
  background-size: cover;
}

.box__square .square:nth-child(2) {
  top: 150px;
  left: -100px;
  width: 120px;
  height: 120px;
  z-index: 2;
  background: url(../img/dollar.png);
  background-position: center;
  background-size: cover;
}

.box__square .square:nth-child(3) {
  bottom: 50px;
  right: -60px;
  width: 80px;
  height: 80px;
  z-index: 2;
  background: url(../img/dollar.png);
  background-position: center;
  background-size: cover;
}

.box__square .square:nth-child(4) {
  bottom: -80px;
  left: 100px;
  width: 50px;
  height: 50px;
  background: url(../img/dollar.png);
  background-position: center;
  background-size: cover;
}

.box__square .square:nth-child(5) {
  top: -80px;
  left: 140px;
  width: 60px;
  height: 60px;
  background: url(../img/dollar.png);
  background-position: center;
  background-size: cover;
}

section .container {
  position: relative;
  width: 700px;
  height: 400px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
  box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  overflow: hidden;
}

section .container.active {
  border-left: 1px solid rgba(255, 255, 255, 0.2);
  border-right: 1px solid rgba(255, 255, 255, 0.5);
}

section .container .user {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
}

section .container .user .imgBx {
  position: relative;
  width: 50%;
  height: 100%;
  transition: 0.5s;
}

section .container .user .imgBx img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

section .container .user .formBx {
  position: relative;
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
  transition: 0.5s;
  /* background-color: #fff; */
}

section .container .user .formBx h2 {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  width: 100%;
  margin-bottom: 10px;
  color: #fff;
}

section .container .user .formBx input {
  width: 100%;
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  border: none;
  border-radius: 35px;
  outline: none;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  font-size: 18px;
  margin: 8px 0;
  letter-spacing: 1px;
  box-shadow: 0 5px 15px rgba(255, 255, 255, 0.2);
}

section .container .user .formBx input::placeholder {
  color: #fff;
}

section .container .user .formBx input[type=submit] {
  max-width: 100px;
  background: #fff;
  color: #666;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 1px;
  transition: 0.5s;
}

section .container .user.signupBx .formBx input[type=submit] {
  background: #fff;
}

section .container .user .formBx .signup {
  position: relative;
  margin-top: 20px;
  font-size: 12px;
  letter-spacing: 1px;
  color: #acacac;
  text-transform: uppercase;
  font-weight: 400;
}

section .container .user .formBx .signup a {
  font-weight: 600;
  text-decoration: none;
  color: #fff;
}

/* Register */
section .container .signupBx {
  /* background-color: white; */
  pointer-events: none;
}

section .container.active .signupBx {
  pointer-events: initial;
}

section .container .signupBx .formBx {
  top: 100%;
}

section .container.active .signupBx .formBx {
  top: 0;
}

section .container .signupBx .imgBx {
  top: -100%;
  transition: 0.5s;
}

section .container.active .signupBx .imgBx {
  top: 0;
}

section .container .siginBx {
  top: 0;
}

section .container.active .siginBx .formBx {
  top: 100%;
}

section .container .siginBx .formBx {
  top: 0;
}

section .container.active .siginBx .formBx {
  top: 100%;
}

section .container .siginBx .imgBx {
  top: 0;
  transition: 0.5s;
}

section .container.active .siginBx .imgBx {
  top: -100%;
}

@media (max-width: 991px) {
  section .container {
    max-width: 400px;
  }

  section .container .imgBx {
    display: none;
  }

  section .container .user .formBx {
    width: 100%;
  }

  section .container.active .siginBx .formBx {
    top: -100%;
  }
}
@media (max-width: 450px) {
  section .container {
    max-width: 280px;
  }

  section .container .user .formBx .signup {
    margin-top: 0;
  }
}